@charset "UTF-8";
body {
  font-family: "lato", sans-serif;
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.5763546798vw;
  }
}
@media (min-width: 1015px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.l-inner {
  max-width: 33.75rem;
  width: 100%;
  height: inherit;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1095px;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.c-footer-logo {
  width: 150px;
  fill: #3e5b9c;
}
@media screen and (min-width: 768px) {
  .c-footer-logo {
    fill: red;
    width: 400px;
  }
}

.c-header-logo {
  width: 300px;
  fill: #b8614b;
}
@media screen and (min-width: 768px) {
  .c-header-logo {
    fill: #182848;
  }
}

.c-logo-svg {
  display: none;
}

/* --- タブレットサイズでブロックになり改行する --- */
.u-block-tab {
  display: inline;
}
@media screen and (min-width: 768px) {
  .u-block-tab {
    display: block;
  }
}

@media screen and (min-width: 600px) {
  .u-hidden-mobile {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .u-hidden-tab {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .u-hidden-desktop {
    display: none;
  }
}

/* --- タブレット小サイズでインラインブロックになり改行させない --- */
.u-inline-sm-tab {
  display: block;
}
@media screen and (min-width: 600px) {
  .u-inline-sm-tab {
    display: inline;
  }
}

/* --- タブレットサイズでインラインブロックになり改行させない --- */
.u-inline-tab {
  display: block;
}
@media screen and (min-width: 768px) {
  .u-inline-tab {
    display: inline;
  }
}

/* --- スマホ時は非表示でタブレット以上だと表示される --- */
.u-show-tab {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-show-tab {
    display: block;
  }
}